import React, { Component } from 'react';
import CategoryList from './CategoryList';
import '../css/CommandList.css';
import commands from '../commands.json';

//parse commands.json using foreach, create a category component and pass the contents of each category to the component

class CommandList extends Component
{
  render()
  {
    let categories = [];
    Object.keys(commands).forEach((category) => {
      categories.push(<CategoryList category={category} key={category} commands={commands[category]}/>);
    });

    return(
     <div className="command-container">
      {categories}
     </div> 
    )
  }
}

export default CommandList;



