import React, { Component } from 'react';
import '../css/CategoryList.css';
import CommandCard from './CommandCard';

class CategoryList extends Component
{
  render()
  {
    let commandCards = [];
    this.props.commands.forEach((command) => {
      commandCards.push(<CommandCard title={command.triggers[0]} key={command.triggers[0]} command={command}/>);
    });
    return(
     <div className="category-container">
      <span className="category-title">{this.props.category}</span>
      <div className="category-commands">
        {commandCards}
      </div>
     </div> 
    )
  }
}

export default CategoryList;



