import React, { Component } from 'react';
import '../css/CommandCard.css';

class CommandCard extends Component
{
  render()
  {
    return(
     <div className="command-card">
      <span className="command-title">{this.props.title}</span>
      <span className="description-title">Description</span>
      <span className="description-content">{this.props.command.description}</span>
      <span className="usage-title">Usage</span>
      <span className="usage-content">{`pls ${this.props.title}`}</span>
      <span className="aliases-title">Aliases</span>
      <span className="aliases-content">{this.props.command.triggers.join(', ')}</span>
     </div> 
    )
  }
}

export default CommandCard;



